//
// QUERY:
//
export default function buildQueryBSMCount(): Query {
  const query = new Query({
    address: new Address(contractAddress),
    func: new ContractFunction("getBSMCount"),
    args: [
      new TokenIdentifierValue(
        Buffer.from(cfgCollection.tokenIdentifier, "utf8"),
      ),
    ],
    // args: [new AddressValue(new Address(address))],
  });
  return query;
}

