export function bmsNftReturnDataToNumer(
  result: QueryResponse,
): number | undefined {
  const [encoded] = result.returnData;
  console.log(result);

  switch (encoded) {
    case undefined:
      // setHasPing(true);
      break;
    // case "":
    //   // setHasPing(false);
    //   break;
    default: {
      const decoded = Buffer.from(encoded, "base64").toString("hex");
      const count = parseInt(decoded, 16);
      console.log("done");
      return count;
      // break;
    }
  }
  console.log("done");
}

