import * as Dapp from "@elrondnetwork/dapp";
import {
  TypedValue,
  Address,
  AddressValue,
  BytesValue,
  BigUIntValue,
  TokenIdentifierValue,
  ContractFunction,
  GasLimit,
  NetworkConfig,
  Query,
} from "@elrondnetwork/erdjs";
import { TransactionPayload } from "@elrondnetwork/erdjs/out";
import { BigNumber } from "bignumber.js";
import { contractAddress, cfgFrmNft, cfgESTDtoken } from "config";
import { RawTransactionType } from "helpers/types";
import { calculateGasLimit } from "./UtilsTransaction";
import * as Constants from "pages/Basic/ConstantsTransaction";

//////////////////////////////////////
// Buy transfer ESTD token
// # Arguments: token identifier, amount (0), campaign (1, 2, 3)
// #
// # token_identifier: TokenIdentifier,
// # amount: BigUint,
// # campaign: u16,
const buildBMSNftArgs = (campaingId: number): TransactionPayload => {
  console.log("buildBMSNftArgs");

  // if (contractAddress === undefined || contractAddress === null) {
  //     // TODO display validation error
  // } else if (collIdentifier === undefined || collIdentifier === null) {
  //     // TODO display validation error
  // }

  const args = [
    new TokenIdentifierValue(Buffer.from(cfgESTDtoken.tokenIdentifier, "utf8")), // colId
    new BigUIntValue(new BigNumber(0)), //amount
    new BigUIntValue(new BigNumber(campaingId)), //campaingId
  ];
  const payload = TransactionPayload.contractCall()
    .setFunction(new ContractFunction("BTEstd"))
    .setArgs(args)
    .build();
  return payload;
};

//
function getPrice4Campaing(campaingId: number): string {
  let campPrice = 0;
  if (campaingId === cfgESTDtoken.campaign_1.id) {
    campPrice = cfgESTDtoken.campaign_1.price;
  } else if (campaingId === cfgESTDtoken.campaign_2.id) {
    campPrice = cfgESTDtoken.campaign_2.price;
  } else if (campaingId === cfgESTDtoken.campaign_3.id) {
    campPrice = cfgESTDtoken.campaign_3.price;
  }
  // return new BigNumber(campPrice).multipliedBy(Constants.EGLD_DECI).toString();
  return campPrice.toString();
}

// transaction
export default function buildBTESTDTransaction(campaingId: number) {
  const dataPayload = buildBMSNftArgs(campaingId);
  const price = getPrice4Campaing(campaingId);
  const gasLimit = calculateGasLimit(
    cfgFrmNft.minGasLimit.createNft,
    dataPayload,
  );

  const pongTr: RawTransactionType = {
    receiver: contractAddress,
    data: dataPayload.toString(),
    value: price,
    gasLimit: gasLimit,
  };
  return pongTr;
}
