import * as bech32 from "bech32";

/**
 * Converts base64 to utf8.
 *
 * @export
 * @param {string} str base64 string to convert
 * @return {string} utf8 result string
 */
export function base64ToUtf(str: string): string {
  return Buffer.from(str, "base64").toString("utf8");
}

/**
 * Converts a base64 string to a number.
 *
 * @export
 * @param {string} str base64 string to convert
 * @return {number} number result
 */
export function base64ToNumber(str: string): number {
  return parseInt(Buffer.from(str, "base64").toString("hex"), 16);
}

/**
 * Converts a base64 string to hex.
 *
 * @export
 * @param {string} str base64 string to convert
 * @return {string} hex result
 */
export function base64ToHex(str: string): string {
  return Buffer.from(str, "base64").toString("hex");
}

/**
 * Converts a base64 string to a bech32 address.
 *
 * @export
 * @param {string} str base64 string to convert
 * @return {string} bech32 address string result
 */
export function base64ToBech32(str: string): string {
  return bech32.encode("erd", bech32.toWords(Buffer.from(str, "base64")));
}

/**
 * Convert array of Hex to number/string
 * skip first item (usually not hex encoded string)
 * @param ecoddedArguments
 * @param slice
 * @returns
 */
export function getRawArgumentsSlice(ecoddedArguments: string[], slice = 1) {
  return ecoddedArguments
    .slice(slice)
    .map((argument) => Buffer.from(argument, "hex"));
}
