  // extract coll ID
  async function extractCollectionIdentifier(data: any, success: boolean) {
    const issueTrData = decodeBase64(data.data);
    let dbg = "";
    dbg += issueTrData;

    // first call can result with incomplete transaction data
    // we make a second call
    console.log(
      "make another regquest callCountqueryTrData=" + callCountqueryTrData,
      " results.length=" + data.results.length,
    );
    if (
      data.results.length < 4 &&
      callCountqueryTrData < cfgFrmNft.queryApiCounter
    ) {
      callCountqueryTrData++;
      displayInfoMessage("Waiting for tranzaction status.");
      await delay(cfgFrmNft.apiRequestDelay.issueNonFungible);
      console.log(
        new Date() + "request callCountqueryTrData=" + callCountqueryTrData,
      );
      queryTrDataCount();
      return;
    }

    // tr0
    dbg += "\n::0) \n";
    const tr0 = decodeBase64(data.results[0].data);
    dbg += tr0;
    let payload = TransactionPayload.fromEncoded(data.results[0].data);
    let ecodedArgs = payload.getEncodedArguments();
    let rawArgs = payload.getRawArguments();
    if (rawArgs[1].toString() === "ok") {
      // displayInfoMessage("SC ok");
    } else {
      displayErrorMessage("Tranzaction error method");
      console.log("Tranzaction error method: " + rawArgs[1].toString());
      // console.log(data);
      return;
    }
    dbg += "\n::ecodedArgs\n";
    dbg += ecodedArgs;
    dbg += "\n::rawArgs\n";
    dbg += rawArgs;

    // tr1
    dbg += "\n::1) \n";
    const tr1 = decodeBase64(data.results[1].data);
    dbg += tr1;
    const resultCallType = data.results[1].callType;
    const resultData = data.results[1].data;
    payload = TransactionPayload.fromEncoded(resultData);
    ecodedArgs = payload.getEncodedArguments();
    rawArgs = payload.getRawArguments();
    const trxMethod = ecodedArgs[0];
    console.log("method:" + trxMethod);

    if (resultCallType !== "1") {
      console.log("Tranzaction error unexpected callType=" + resultCallType);
      return;
    }
    if (trxMethod !== "issueNonFungible") {
      displayErrorMessage("Tranzaction error issue tocken");
      console.log("Tranzaction error unexpected method: " + trxMethod);
      return;
    }
    dbg += "\n:: " + trxMethod + "\n";
    dbg += ecodedArgs;
    dbg += "\n::raw\n";
    dbg += rawArgs;

    // tr2
    // ins case of success should contain COLL_ID
    dbg += "\n::2) \n";
    processResultCollectionIdentifier(data.results[2], dbg);

    // tr3
    dbg += "\n::3) \n";
    processResultCollectionIdentifier(data.results[3], dbg);

    dbg += "\n::contractAddress\n";
    dbg += contractAddress;
    dbg += "\n::contractAddress Bech32:\n";
    const contractBech32 = converters.base64ToBech32(contractAddress);
    dbg += contractBech32;

    console.log("=== after send tranzaction");
    console.log(data);
    setDbgMsg(dbg);
  }

