  const processResultCollectionIdentifier = (result: any, dbg: string) => {
    // tr
    const payload = TransactionPayload.fromEncoded(result.data);
    const ecodedArgs = payload.getEncodedArguments();
    const rawArgs = payload.getRawArguments();
    dbg += "\n::ecodedArgs\n";
    dbg += ecodedArgs;
    dbg += "\n::rawArgs\n";
    dbg += rawArgs;

    // SC Tranzaction responce - ignore
    if (result.callType !== "2") {
      return false;
    }

    // ::ecodedArgs
    // ,04,6e6f7420656e6f75676820676173
    // ::rawArgs
    // ,,not enough ga
    //
    const resultCode = ecodedArgs[1];
    if (resultCode === "04") {
      // Tranzaction ERROR!
      displayErrorMessage(result.returnMessage);
      console.log("Tranzaction error message: " + result.returnMessage);
      return false;
    }

    // tranzaction success - save collID
    // @ok@434f4c332d313130306539
    // ok, COL3-1100e9
    //
    const indexColId = 2;
    const collId = rawArgs[indexColId];
    dbg += collId;
    if (collId === undefined || collId === null) {
      displayErrorMessage(result.returnMessage);
      console.log("Tranzaction error missing coll ID:" + collId);
      return false;
    }

    // UPDATE coll ID !!!
    setCollIdentifier(collId.toString());
    displayInfoMessage(
      "Collection " + collId.toString() + " created succefully",
    );

    // hide step1 and show step2
    setIsStepCreateColl(false);
    setIsStepSetRole(true);

    dbg += "\n::callType === 2\n";
    dbg += collId.toString();
    console.log("collId:" + collId);
    return true;
  };

