  const processResultCreateNftItem = (result: any): boolean => {
    // tr
    const payload = TransactionPayload.fromEncoded(result.data);
    const ecodedArgs = payload.getEncodedArguments();
    // const rawArgs = payload.getRawArguments();
    const rawArgs = converters.getRawArgumentsSlice(ecodedArgs);
    // const method = ecodedArgs[0];
    console.log("processResultCreateNftItem");
    console.log(result.data);
    console.log(ecodedArgs);
    console.log(rawArgs.toString());

    // error message should stop
    if (result.returnMessage !== undefined && result.returnMessage !== null) {
      // Tranzaction ERROR!
      displayErrorMessage(
        "Tranzaction error create nft: " + result.returnMessage,
      );
      console.log("Tranzaction error message: " + result.returnMessage);
      return false;
    }

    // SC Tranzaction responce - result OK
    // const resultCode = ecodedArgs[1];
    if (result.callType === "0") {
      const resultCode = rawArgs[0]; // ok
      const nftNonce = ecodedArgs[2]; // 1
      if (
        resultCode !== undefined &&
        resultCode !== null &&
        nftNonce !== undefined &&
        nftNonce !== null &&
        resultCode.toString() === "ok"
      ) {
        // Tranzaction SUCCESS!
        setCollectionItemNonce(nftNonce.toString());
        setIsStepTransferNft(true);
        displayInfoMessage("NFT created successfully: " + nftNonce);
        console.log("NFT created successfully: " + nftNonce);
        return true;
      } else {
        // Tranzaction ERROR!
        displayErrorMessage(
          "Tranzaction error create nft: " + result.returnMessage,
        );
        console.log("Tranzaction error message: " + result.returnMessage);
        return false;
      }
    }
    // console.log("nft id:" + collIdentifier);
    return false;
  };

