  async function processResultTransferNft(data: any, success: boolean) {
    // retry
    console.log(
      "make another regquest callCountqueryTrData=" + callCountqueryTrData,
      " results.length=" + data.results.length,
    );
    if (
      data.results.length < 1 &&
      callCountqueryTrData < cfgFrmNft.queryApiCounter
    ) {
      callCountqueryTrData++;
      displayInfoMessage("Waiting for tranzaction create nft status.");
      await delay(cfgFrmNft.apiRequestDelay.transferNft);
      console.log(
        new Date() + "request callCountqueryTrData=" + callCountqueryTrData,
      );
      queryTrDataCount();
      return;
    }

    // transfer error
    // if (data.results.length === 1) {
    //   displayErrorMessage("Tranzaction error transfer nft");
    //   console.log("Tranzaction error message: ");
    //   return false;
    // }

    // iterate results
    data.results.forEach((result: any) => {
      const res = processResultTransferNftItem(result);
      if (res) {
        // return on ok result
        console.log("processResultCreateNft true");
        return true; // return only from callback
      }
    });

    // console.log("processResultCreateNft false");
    // return false; // not working
  }

  //
  const processResultTransferNftItem = (result: any): boolean => {
    // tr
    const payload = TransactionPayload.fromEncoded(result.data);
    const ecodedArgs = payload.getEncodedArguments();
    // const rawArgs = payload.getRawArguments();
    const rawArgs = converters.getRawArgumentsSlice(ecodedArgs);
    const method = ecodedArgs[0];
    console.log("processResultTransferNftItem");
    console.log(result.data);
    console.log(ecodedArgs);
    console.log(rawArgs.toString());

    // error message should stop
    if (result.returnMessage !== undefined && result.returnMessage !== null) {
      // Tranzaction ERROR!
      displayErrorMessage(
        "Tranzaction error tranfer nft: " + result.returnMessage,
      );
      console.log("Tranzaction error message: " + result.returnMessage);
      return false;
    }

    // SC Tranzaction responce - result OK
    if (result.callType === "1" && method === "ESDTNFTTransfer") {
      const collId = rawArgs[0]; // ok
      const nftNonce = ecodedArgs[2]; // 1
      if (
        collId !== undefined &&
        collId !== null &&
        nftNonce !== undefined &&
        nftNonce !== null
      ) {
        // Tranzaction SUCCESS!
        setCollectionItemNonce(nftNonce.toString());
        setIsStepTransferNft(true);
        displayInfoMessage(
          "NFT tranfered successfully: " + collId + "-" + nftNonce,
        );
        console.log("NFT created successfully: " + collId + "-" + nftNonce);
        return true;
      } else {
        // Tranzaction ERROR!
        displayErrorMessage(
          "Tranzaction error transfer nft: " + result.returnMessage,
        );
        console.log(
          "Tranzaction error transfer nft message: " + result.returnMessage,
        );
        return false;
      }
    }
    // console.log("nft id:" + collIdentifier);
    return false;
  };

