import axios from "axios";

interface GetLatestTransactionsType {
  apiAddress: string;
  address: string;
  contractAddress: string;
  timeout: number;
  page?: number;
  url?: string;
  txHash?: string;
  creator?: string;
}

// async function apiRequest(url: string, params: any) {
//   return { data } = await axios.get(url, params);
// };

const fetchTransactions = (url: string) =>
  async function getTransactions({
    apiAddress,
    address,
    contractAddress,
    timeout,
    txHash = "",
  }: GetLatestTransactionsType) {
    try {
      const { data } = await axios.get(`${apiAddress}${url}${txHash}`, {
        params: {
          sender: address,
          receiver: contractAddress,
          creator: contractAddress,
          condition: "must",
          size: 25,
        },
        timeout,
      });

      return {
        data: data,
        success: data !== undefined,
      };
    } catch (err) {
      return {
        success: false,
      };
    }
  };

const fetchParamApi = (url: string) =>
  async function getTransactions({
    apiAddress,
    address,
    contractAddress,
    timeout,
    txHash = "",
  }: GetLatestTransactionsType) {
    try {
      const urlFull = `${apiAddress}/accounts/${contractAddress}${url}`;
      const { data } = await axios.get(urlFull, {
        params: {
          sender: address,
          receiver: contractAddress,
          creator: contractAddress,
          condition: "must",
          size: 25,
        },
        timeout,
      });

      return {
        data: data,
        success: data !== undefined,
      };
    } catch (err) {
      return {
        success: false,
      };
    }
  };

const fetchApi = (url: string) =>
  async function getTransactions({
    apiAddress,
    address,
    contractAddress,
    timeout,
    txHash = "",
  }: GetLatestTransactionsType) {
    try {
      const urlFull = `${apiAddress}/accounts/${contractAddress}${url}`;
      const { data } = await axios.get(urlFull, {
        params: {},
        timeout,
      });

      return {
        data: data,
        success: data !== undefined,
      };
    } catch (err) {
      return {
        success: false,
      };
    }
  };

export const getTransactions = fetchTransactions("/transactions");
export const getTransactionsCount = fetchTransactions("/transactions/count");

