import { BigNumber } from "bignumber.js";
import {
  TypedValue,
  Address,
  AddressValue,
  BytesValue,
  BigUIntValue,
  TokenIdentifierValue,
  ContractFunction,
  GasLimit,
  NetworkConfig,
  Query,
} from "@elrondnetwork/erdjs";
import { TokenType, TransactionPayload } from "@elrondnetwork/erdjs/out";

/**
 * calculate aproximative gas amount for tranzaction
 * // Martin advice:
   cfgFrmNft.minGasLimit.issueNonFungible +
   dataPayload.length() *
   NetworkConfig.getDefault().GasPerDataByte;
 */
export function calculateGasLimit(
  fixedGasLimit: number,
  dataPayload: TransactionPayload,
): number {
  const gasLimit =
    fixedGasLimit +
    dataPayload.length() * NetworkConfig.getDefault().GasPerDataByte;
  return gasLimit;
}

