import React from "react";
import * as Dapp from "@elrondnetwork/dapp";
import routes, { routeNames } from "routes";
import { useLocation, useParams, useRouteMatch } from "react-router-dom";
import Footer from "./Footer";
import Navbar from "./Navbar";

import LayoutMarket from "./LayoutMarket";

const Layout = ({ children }: { children: React.ReactNode }) => {
  const { pathname } = useLocation();
  // const { type } = useParams();
  const { path } = useRouteMatch();

  const { loggedIn } = Dapp.useContext();
  const refreshAccount = Dapp.useRefreshAccount();

  React.useEffect(() => {
    if (loggedIn) {
      refreshAccount();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [loggedIn]);

  // NEW part
  const layoutType = () => {
    console.log("pathname=" + pathname);
    // console.log("type=" + type);
    console.log("path=" + path);
    return 1; // FUN
    // switch (pathname) {
    //   case "/fun123":
    //     return 1; // FUN
    //   default:
    //     return 0; // market
    // }
  };
  // return <div>{project()}</div>;

  // OLD part
  // return (
  //   <div className="bg-light d-flex flex-column flex-fill wrapper">
  //     <Navbar />
  //     <main>
  //       <Dapp.Authenticate routes={routes} unlockRoute={routeNames.unlock}>
  //         {children}
  //       </Dapp.Authenticate>
  //     </main>
  //     <Footer />
  //   </div>
  // );

  // TRY NEW
  const layoutAuthenticate = () => {
    switch (layoutType()) {
      case 1:
        // FUN
        return (
          <div className="iq-fun-color-white">
            <main>
              <Dapp.Authenticate
                routes={routes}
                unlockRoute={routeNames.unlock}
              >
                {children}
              </Dapp.Authenticate>
            </main>
          </div>
        );
      default:
        // Market
        return (
          <div className="bg-light d-flex flex-column flex-fill wrapper">
            <Navbar />
            <main>
              <Dapp.Authenticate
                routes={routes}
                unlockRoute={routeNames.unlock}
              >
                <LayoutMarket> {children} </LayoutMarket>
              </Dapp.Authenticate>
            </main>
            <Footer />
          </div>
        );
    }
  };

  return <div>{layoutAuthenticate()}</div>;
};

export default Layout;
