import axios from "axios";
import { cfgCollection } from "config";

interface GetGatewayType {
  apiAddress?: string;
  timeout: number;
  page?: number;
  url?: string;
  txHash?: string;
  creator?: string;
}

// async function apiRequest(url: string, params: any) {
//   return { data } = await axios.get(url, params);
// };

const fetchIPFSFileApi = (url: string) =>
  async function getIPFSFile({ apiAddress, timeout }: GetGatewayType) {
    try {
      const { data } = await axios.get(`${cfgCollection.apiPath}/${url}`, {
        params: {
          // size: 25,
        },
        timeout: 5000,
      });

      return {
        data: data,
        success: data !== undefined,
      };
    } catch (err) {
      console.error(err);
      return {
        success: false,
      };
    }
  };

////https://ipfs.io/ipfs/QmXatUvUaQzDmHAogwkizcTCt8jPCWAVta1TfpX2Fo4khE/_metadata.json
export const getCollectionJson = fetchIPFSFileApi(
  `${cfgCollection.collCID}/${cfgCollection.collName}`,
);
