/////////////////////////////
export interface NftApiType {
  identifier?: string;
  collection?: string;
  attributes?: string;
  nonce?: 19;
  type?: string;
  name?: string;
  creator?: string;
  royalties?: number;
  url?: string;
  media?: string;
  ticker?: string;
}

// build IPFS url
const buildIPFSUrl = (collItem: CollectionType): string => {
  let res = `${cfgCollection.apiPath}/${cfgCollection.collCID}`;
  res += collItem.image.replace("undefined", ""); //"image": "undefined/1.png",
  return res;
};

/**
 * Transform Json to NFT tags format
  {
    "trait_type": "Background",
    "value": "bluegreen1"
  }
  =>
  attr1:tr1;attr2:tr2
 */
const buildNFTTags = (collItem: CollectionType): string => {
  let res = "";
  for (const attr of collItem.attributes) {
    res += attr.trait_type + ":" + attr.value + ";";
  }
  return res;
};

// 1 EGLD decimals
const EGLD_DECI = new BigNumber(1000000000000000000);

//////////////////////////////////////
// Create NFT - ERD call
/**
    # token_identifier: TokenIdentifier,
    # amount: BigUint,
    # name: ManagedBuffer,
    # royalties: BigUint,
    # hash: ManagedBuffer,
    # attributes: ManagedBuffer,
    # uri1: ManagedBuffer,
    # uri2: ManagedBuffer,
    # uri3: ManagedBuffer,
    # sell_price: BigUint,
 */
const buildBMSNftArgs = (collItem: CollectionType): TransactionPayload => {
  console.log("buildBMSNftArgs");

  // if (contractAddress === undefined || contractAddress === null) {
  //     // TODO display validation error
  // } else if (collIdentifier === undefined || collIdentifier === null) {
  //     // TODO display validation error
  // }

  // const collId: string = collIdentifier!;
  // // CDDD-29f066          :[434444442d323966303636]
  const tokenIdentifier: string = cfgCollection.tokenIdentifier!; // TODO take value from select list
  const colItemName: string = collItem.name!;
  const colItemRoyalties: number = cfgCollection.royalties!; //collectionItemRoyalties! * 100; // 100 = 1%
  const colItemTags: string = buildNFTTags(collItem);
  // const colItemQta: number = collectionItemQta! * 100;
  const colItemUrl: string = buildIPFSUrl(collItem);

  const hash = crypto.createHash("sha256");
  hash.update(colItemName);
  const colItemHash = hash.digest("hex");

  const args = [
    new TokenIdentifierValue(Buffer.from(tokenIdentifier, "utf8")), // colId
    new BigUIntValue(new BigNumber(1)), //amount
    BytesValue.fromUTF8(colItemName), //name
    new BigUIntValue(new BigNumber(cfgCollection.royalties)), //royalties
    BytesValue.fromUTF8(colItemHash), //hash
    BytesValue.fromUTF8(colItemTags), //attr
    BytesValue.fromUTF8(colItemUrl), //uri (www.nfts.com)
    BytesValue.fromUTF8(""), //uri (www.nfts.com)
    BytesValue.fromUTF8(""), //uri (www.nfts.com)
    new BigUIntValue(new BigNumber(3).multipliedBy(EGLD_DECI)), // withdraw_amount:

    // TEST data
    // new TokenIdentifierValue(Buffer.from("QH7-764c51", "utf8")), // colId
    // new BigUIntValue(new BigNumber(1)), //amount
    // BytesValue.fromUTF8("QH7ITEM1"), //name
    // new BigUIntValue(new BigNumber(1000)), //royalties
    // BytesValue.fromUTF8("9223372036854775807"), //hash
    // new BigUIntValue(new BigNumber(66051)), // code
    // BytesValue.fromUTF8("attr1:tr1;attr2:tr2"), //attr
    // BytesValue.fromUTF8("www.nfts.com"), //uri (www.nfts.com)
    // BytesValue.fromUTF8("www.nfts.com"), //uri (www.nfts.com)
    // BytesValue.fromUTF8("www.nfts.com"), //uri (www.nfts.com)
  ];
  // const payload = new TransactionPayload("ping pong");
  const payload = TransactionPayload.contractCall()
    .setFunction(new ContractFunction("BMSNft"))
    .setArgs(args)
    .build();
  //
  // setDbgMsg(payload.toString());
  //
  return payload;
};

// transaction
// const buildBMSNftTransaction = (): RawTransactionType => {
export default function buildBMSNftTransaction(collItem: CollectionType) {
  const dataPayload = buildBMSNftArgs(collItem);
  const gasLimit = calculateGasLimit(
    cfgFrmNft.minGasLimit.createNft,
    dataPayload,
  );
  const pongTr: RawTransactionType = {
    receiver: contractAddress,
    data: dataPayload.toString(),
    value: cfgCollection.nftPrice,
    gasLimit: gasLimit,
  };
  return pongTr;
}
