import * as React from "react";
import { useState } from "react";

import * as Dapp from "@elrondnetwork/dapp";
import { Form, Button } from "react-bootstrap";
import { contractAddress } from "config";
import Denominate from "../../components/Denominate";

const ContactInfo = () => {
  // const {
  //   address,
  //   account: { balance },
  // } = Dapp.useContext();

  // const MyComponent = () => {
  //   const [value, setValue] = React.useState('some initial value');
  //   return <input value={value} onChange={e => setValue(e.target.value)} />;
  // }

  const contact = {
    email: "",
    collectionName: "",
    nuances: "1.1",
  };

  const [email, setEmail] = useState("");
  const [collectionName, setCollectionName] = useState("");
  const [nuances, setNuances] = useState("");

  return (
    <>
      <div className="text-white" data-testid="contactInfo">
        <div className="mb-1">
          <span className="opacity-6 mr-1">Your address:</span>
          <span data-testid="accountAddress"> {contact.email}</span>
        </div>
        <div className="mb-4">
          <span className="opacity-6 mr-1">Contract address:</span>
          <span data-testid="contractAddress"> {contractAddress}</span>
        </div>
        <div>
          <h3 className="py-2">
            <Denominate value={contact.nuances} dataTestId="balance" />
          </h3>
        </div>
      </div>

      <div>
        <Form>
          <Form.Group controlId="formBasicEmail">
            <Form.Label>Email address</Form.Label>
            <Form.Control type="email" placeholder="Enter email" />
            <Form.Text className="text-muted">
              We will never share your email with anyone else: {contact.email}
            </Form.Text>
          </Form.Group>

          <Form.Group controlId="formBasicPassword">
            <Form.Label>Password</Form.Label>
            <Form.Control type="password" placeholder="Password" />
          </Form.Group>

          <Form.Group controlId="formBasicCheckbox">
            <Form.Check type="checkbox" label="Check me out" />
          </Form.Group>

          <Form.Group controlId="formBasicCheckbox">
            <Form.Label>Email address</Form.Label>
            <Form.Control
              type="text"
              placeholder="Enter email"
              onChange={(e) => {
                setEmail(e.target.value);
              }}
            />
            <Form.Text className="text-muted">
              We will never share your email with anyone else: {email}
            </Form.Text>
          </Form.Group>

          <Button variant="primary" type="submit">
            Submit
          </Button>
        </Form>
      </div>
    </>
  );
};
export default ContactInfo;
